.profile__info {
    display: grid;
    gap: 16px 18px;
    grid-template-columns: 1fr 24px;
}

@media screen and (max-width: 686px) {
  .profile__info {
    grid-template-columns: 210px 18px;
    gap: 14px 0;
    justify-content: center;
    margin-bottom: 36px;
    align-items: center;
  }
}